@import 'reset';

@import 'variable';

body{
  font-family: $f-family;
  
}



.wrap{
  width: 940px;
  margin: 0 auto;
  margin-top: 30px;
  margin-bottom: 60px;
}

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  

  .icon a img{
    width: 210px;
    height: 40px;
  }

  .nav{
    display: flex;
    align-items: center;
    li a{
      
      color: $color-1;
      font-weight: bold;
      text-decoration: none;
      padding: 10px 30px;
    }
    a:hover{
        background-color: $color-1;
        color: aliceblue;
    }
    .shopping{
      margin-left: 49px;
      
    }
    .shopping a:hover{
      background: none;
    }
  }
}

.banner{
  max-width: 100%;
  margin-top: 30px;
  margin-bottom: 60px;
}

.content{
  display: flex;
  justify-content: space-between;
}

.menu{
  width: 300px;
  text-align: center;
  font-weight: bold;
  font-size: $font-md*1.5;
  
  h1{
    background-color: $color-1;
    padding: 20px 0;
    a{
      color: $color-3;
    text-decoration: none;
    }
  }

  h2{
    background-color: $color-3;
    padding: 20px 0;
    border: 1px solid $color-3;
    a{
      color: $color-1;
    text-decoration: none;
    }
  }

  h3{
    padding: 20px 0;
    border: 1px solid $color-3;
    a{
      color: $color-1;
    text-decoration: none;
    }
  }
}

.data{
  width: 620px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .product{
    color: $color-1;
    margin-bottom: 20px;
    position: relative;
    width: 300px;
    display: flex;
    flex-direction: column;
    .tage{
      position: absolute;
      background-color: $color-1;
      color: $color-3;
      padding: 10px;
      font-weight: bold;
      left: 20px;
      writing-mode: vertical-rl;
      /* 直書語法 writing-mode: vertical-rl; */ 
    }


    .favor{
      position: absolute;
      width: 24px;
      height: 24px;
      top:19px;
      right:22px;
    }

    ul{
      display: flex;
      flex-wrap: wrap;
      text-align: center;
      .name{
        font-size: $font-md*1.25;
        padding: 14px 33px;
        border: 1px solid $color-3;
      }

      .price{
        font-size: $font-md*1.25;
        font-weight: bold;
        padding: 14px 25px 14px 24px;
        border: 1px solid $color-3;
      }

      .likes{
        font-size: $font-md*1.5;
        font-weight: bold;
        padding: 16px 90px;
        background-color: $color-3;
        a{
          text-decoration: none;
          color: $color-1;
        }
        
      }
    }
  }
  
}

.data .page{
  display: flex;
  justify-content: flex-end;
  font-size: $font-md*1.25;
  width: 100%;
  height: 60px;
  margin-top: 30px;
  
  li{
    border: 1px solid $color-3;
    width: 58px;
    height: 58px;
    display: flex;
    align-items: center;
    justify-content: center;
    a{
      color: $color-1;
      text-decoration: none;
      padding: 15px;
    }
    
  }
  li:hover{
    background-color: $color-3;
  }
}